/*CONTENT*/

.content {
  padding: 0px 25px 0px;
  width: 100vw;
}

/*PRINCIPAL CONTAINER*/

.principal-infoContainer {
  margin-top: 30px;
  height: 250px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

.principal-leftbox {
  height: 190px;
  width: 320px;
}

.principal-textBoxContainer {
  font-weight: 600;
  line-height: 1.6;
}

.principal-buttonBoxContainer {
  margin-top: 20px;

  display: flex;
  flex-direction: row;
}
.principal-buttonBoxContainer button:nth-child(1) {
  margin-left: -1px;
}

.principal-buttonBoxContainer button:nth-child(2) {
  background: var(--branco);
  color: var(--corQuatro);
  border: var(--corQuatro) 2px solid;
}

.principal-buttonBoxContainer button:nth-child(2) a {
  text-decoration: none;
  color: var(--corQuatro);
}

.principal-rigthbox img {
  width: 400px;
  height: 300px;
}

/*SECONDARY CONTAINER*/

.secondary-infoContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  margin-top: 40px;
  padding: 0px 100px 0px;
  height: 150px;
  width: 100%;
}

.secondary-infoBox {
  width: 30%;
  height: 100px;
  font-size: 0.8rem;
  font-weight: 600;
}

.secondary-imageBox img {
  width: 55px;
  height: 45px;
}

/*SEARCH CONTAINER*/

.search-infoContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
}

.search-titleBox {
  color: var(--corQuatro);
  margin-bottom: 40px;
}
.search-titleBox h1 {
  font-weight: 600;
}

.search-filterBox {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-bottom: 50px;
}

/*DISPLAY PART*/
.search-displayBox {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 30px;
}

.search-cardReceptor {
  cursor: pointer;
  background-color: var(--branco);
  margin-bottom: 60px;
  width: 400px;
  height: 250px;
  border-radius: 10px;
  -webkit-box-shadow: 3px 3px 5px 3px #ccc;
  -moz-box-shadow: 3px 3px 5px 3px #ccc;
  box-shadow: 3px 3px 5px 3px #ccc;

  padding: 30px;
}

.search-cardReceptor > p {
  color: var(--corQuatro);
  font-size: 1rem;
  font-weight: 800;
  margin-bottom: 5px;
}

.search-infoReceptor {
  display: flex;
  align-items: center;
  flex-direction: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  height: 120px;
}

.search-infoReceptor img {
  border-radius: 50%;
  height: 80px;
  width: auto;
}

.search-infoReceptor .info {
  font-size: 0.75rem;
  font-weight: 500;
  line-height: 1.7;
}

.search-porcentContainer {
  display: flex;
  flex-direction: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  align-items: center;
  height: 50px;
}

.search-porcentContainer p {
  width: 25%;
  color: var(--corQuatro);
  font-weight: 800;
}

.porcentageBar {
  width: 250px;
  height: 25px;
  border-radius: 15px;
  background-color: #e5e5e5;
}

.concludedBar {
  top: 0;
  left: 0;
  width: 100%;
  height: 25px;
  border-radius: 15px;
  background-color: var(--corQuatro);
}

.doacaoDivIndisponivel {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 60px;
}
.doacaoDivIndisponivel #doacaoIndisponivel {
  text-align: center;
  opacity: 0.6;
  font-size: 2rem;
}

@media (max-width: 1190px) {
  .search-displayBox {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
  }
}

@media (max-width: 870px) {
  .search-displayBox {
    display: grid;
    grid-template-columns: 1fr;
    column-gap: 30px;
  }
}

@media (max-width: 600px) {
  .principal-infoContainer {
    display: flex;
    flex-direction: column;
  }

  .principal-leftbox {
    display: flex;
    flex-direction: column;
    margin-top: 110px;
  }

  .principal-rigthbox {
    display: none;
  }

  .secondary-infoContainer {
    display: flex;
    justify-content: space-between;
  }

  .search-infoContainer {
    margin-top: 150px;
  }

  .secondary-infoBox {
    margin: 0 auto;
    margin-left: 10px;
  }
}
